import axios from "axios";
import React, { useState } from "react";
import { api } from "../base_url";
import { toast } from "react-toastify";
import { useNavigate, useParams } from "react-router-dom";
const emailRegex = RegExp(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/);

function Create_password() {
  const [hide, sethide] = useState(false);
  const [verifytoken , setverifytoken] = useState("")
  const [createpass, setcreatepass] = useState(false);
  const [message, setmessage] = useState("");
  const navigate = useNavigate()
  const {verify_token} = useParams()
  const [contact, setcontact] = useState({
    email: "",
    newpass: "",
    newpassconfirm: "",
    otp: "",
  });
  const [errorfield, seterrorfield] = useState({
    email: "",
    otp: "",
    newpass: "",
    newpassconfirm: "",

  });
  console.log(contact?.email);
 
  const createNewpass = async () => {
    let data = JSON.stringify({
        password: contact?.newpass,
        verify_token: verify_token,
        password_confirmation : contact?.newpassconfirm
    });

    let config = {
      method: "post",
      maxBodyLength: Infinity,
      url: api + "/api/createNewPassword",
      headers: {
        "Content-Type": "application/json",
      },
      data: data,
    };
if(contact?.newpass == contact?.newpassconfirm ) {
  await  axios
      .request(config)
      .then((response) => {
        console.log(JSON.stringify(response.data));
        if (response.data?.status == true) {
            toast.success(response.data?.message);
            setTimeout(() => {
                navigate("/login");      
            }, 2000);
        }
       
    
      })
      .catch((error) => {
        console.log(error);
        // toast.error(error.message)
      });

} else{
    toast.error("Password and Confirm Password Must Be Same !")
}

  };

  const logins_field2 = (e) => {
    switch (e.target.name) {
      case "email":
        errorfield.email = emailRegex.test(e.target.value)
          ? ""
          : "Email not valid.";
        break;
      case "otp":
        errorfield.otp = e.target.value
          ? ""
          : "Email not valid.";
        break;

      default:
        break;
    }
    seterrorfield({ ...errorfield });
    setcontact({ ...contact, [e.target.name]: e.target.value });
  };
  const logins_field = (e) => {
    console.log(e);
    switch (e) {
      case "email":
        errorfield.email = contact.email == "" ? "required" : "";
        break;
      case "otp":
        errorfield.otp = contact.otp == "" ? "required" : "";
        break;
      default:
        break;
    }
    seterrorfield({ ...errorfield });
    // setcontact({ ...contact, [e.target.name]: e.target.value })
  };

  return (
    <>
      <div className="breadcrumbs">
        <div className="container aos-init aos-animate" data-aos="fade-up">
          <ul>
            <li>
              <a href="/">Home</a>
            </li>
            <li>
              <a href="/login"> Sign in</a>
            </li>

            <li>
              <a href="#">
                <span>Create New Password</span>
              </a>
            </li>
          </ul>
        </div>
      </div>

      <div className="forget-password font-lg">
        <div className="container">
          <h1 data-aos="fade-up">Create New Password</h1>
          <br />
          <br />
          <div className="row">
            <div className="col-md-12 col-md-offset-4" data-aos="fade-up">
              <div className="panel panel-default">
                  <div className="text-center">
                    <h3> Create New Password</h3>
                    <div className="panel-body">
                      <form
                              className="form"
                              autoComplete="off"
                      >
                        <div className="form-group">
                          <div className="input-group">
                            <input
                              id="newpass"
                              name="newpass"
                              onChange={(e) => logins_field2(e)}
                              placeholder="Enter New Password *"
                              className="form-control"
                              type="password"
                              autoComplete="off"
                              style={
                                errorfield.newpass == ""
                                  ? {}
                                  : { borderBottom: "1px solid red" }
                              }
                            />

                            <input
                              name="newpassconfirm"
                              onChange={(e) => logins_field2(e)}
                              placeholder="Enter Confirm Password *"
                              className="form-control"
                              type="password"
                              autoComplete="off"
                              style={
                                  errorfield.newpassconfirm == ""
                                  ? {marginTop : "12px"}
                                  : { borderBottom: "1px solid red" ,marginTop : "12px" }
                                }
                            />
                          </div>
                          
                        </div>
                        <div className="form-group">

                          <button
                            // name="recover-submit"
                            onClick={() => {
                              if (errorfield.email == "") {
                                createNewpass();
                                console.log("clicked");
                              } else {
                                logins_field("newpass");
                                console.log("clicked 444");

                                //   errorfield.email = "required";
                              }
                            }}
                            className="btn btn-lg btn-secondary btn-block"
                            type="button"
                          >
                            Submit
                          </button>
                          {/* </a> */}
                        </div>
                      </form>
                    </div>
                  </div>
                
              </div>
            </div>
          </div>
          <br />
          <br />
        </div>
      </div>
    </>
  );
}

export default Create_password;
