import React from "react";
import { useNavigate } from "react-router-dom";

export default function Add_user (props){
const navigate = useNavigate()
    return(
            
            <div class={(props.sidebar ? "active " : " ") + "router-body"}>
                <div class="breadcrumbs" data-aos="fade-down">
                    <ul>
                        <li><a href="/dashboard">Dashboard </a></li>
                        <li><a href="/dashboard">
                        {localStorage.getItem("user_type") == "Both" ? props.supplier  : localStorage.getItem("user_type")  } 
                              </a></li>
                        <li><a href="#"><span> User Management </span></a></li>
                        <li><a href="#"><span>Add New User </span></a></li>
                    </ul>
                </div>
                <div class="product_prfile">
                    <h1>Add New User</h1>
                    <div class="row">
                        <div class="col_left">
                            <div class="panel">
                                <div class="form-group full">
                                    <input type="text" placeholder="First Name" class="form-control"/>
                                </div>
                                <div class="form-group full">
                                    <input type="text" placeholder="Last Name" class="form-control"/>
                                </div>
                                <div class="form-group input-group">
                                    <input id="email" name="email" placeholder="Email Address" class="form-control" type="email"/>
                                </div>
                              
                                <div class="form_wrap row">
                                    <div class="form-group full Company_sector">
                                        <input placeholder="Contact Number" class="form-control" type="tel"/>
                                    </div>
                                    <div class="form-group">
                                        <div class="custom-select">
                                            <select>
                                                <option>Roles</option>
                                                <option>ui</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="button_wrap row">
                                <a href="" class="btn btn-secondary">Add User</a>
                                <a href="" class="btn btn-primary">Cancel</a>
                            </div>
                            <div class="error-button row justify-content-center">
                                <a class="error_icon" onClick={()=>navigate(-1)} style={{cursor:"pointer"}}><i class="fa fa-arrow-left left" aria-hidden="true"></i>Back to User Management</a>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
    )
}