import React, { useEffect, useState } from "react";
import InputWithButton from "../../components/input-with-button/input-with-button";
import { api } from "../base_url";
import Left_menu from "../productpages/left_menu";
import { country } from "./country";
import {  ToastContainer, toast } from 'react-toastify';
import { useNavigate } from "react-router-dom";

const emailRegex = RegExp(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/);

const url = RegExp(
  "^((((h|H)(t|T)|(f|F))(t|T)(p|P)((s|S)?)://[-.\\w]*)|(((w|W){3}\\.)[-.\\w]+))(/?)([-\\w.?,:'/\\\\+=&;%$#@()!~]*)?$"
);

function Add_new_product(props) {
  const [image, setImage] = useState("");
  const [video, setvideo] = useState([]);
  const [AnswerError, setAnswerError] = useState("");

console.log(AnswerError);

  const [contact, setcontact] = useState({
    p_name: "",
    ps_name: "",
    category: "",
    s_category: "",
    country: "",
    c_name: "",
    Description: "",
    product_file: [],
    thumb: "",
    Policy: "",
    Quantity: "",
    Guarantee: "",
    Creation: "",
    yt_link: "",
  });
  const [errorfield, seterrorfield] = useState({
    p_name: "",
    ps_name: "",
    category: "",
    s_category: "",
    country: "",
    c_name: "",
    Description: "",
    product_file: [],
    thumb: "",
    Policy: "",
    Quantity: "",
    Guarantee: "",
    Creation: "",
    yt_link: "",
  });

  const [category, setcategory] = useState([
    {
      category: "Equipment",
      subcategory: [
        { category: "Anti-ageing" },
        { category: "Slimming" },
        { category: "Hair removal" },
        { category: "Permanent make-up" },
        { category: "Skin Diagnosis" },
      ]
    },
    {
      category: "Medical Devices",
      subcategory: [
        { category: "Fillers" },
        { category: "Injectables" },
        { category: "Microneedling" },
        { category: "Mesotherapy" },
        { category: "PRP" },
      ]
    },
    {
      category: "Toxin",
      subcategory: [
        { category: "Botox" },
      ]
    },
    {
      category: "Plastic surgery",
      subcategory: [
        { category: "Implants" },
        { category: "Threads" },
        { category: "Compression garnements" },
      ]
    },
    {
      category: "Cosmetics",
      subcategory: [
        { category: "Medical - Cosmeceuticals" },
        { category: "Professional" },
      ]
    },
    {
      category: "Chemical peels",
      subcategory: [
        { category: "Light" },
        { category: "Medium" },
        { category: "Deep" },
      ]
    },
    {
      category: "Nutricosmetic",
      subcategory: [
        { category: "Skin" },
        { category: "Hair" },
        { category: "Solar" },
      ]
    },
    {
      category: "Cabin and salon furniture",
      subcategory: [
        { category: "Treatment Table" },
      ]
    },
    {
      category: "Coming soon",
      subcategory: [
        { category: "Eyelash Extension" },
        { category: "Nails" },
        { category: "Makeup" },
        { category: "Decorative cosmeticss" },
      ]
    }
  ])

  const [subcategory, setsubcategory] = useState([])
  const logins_field2 = (e) => {
    switch (e.target.name) {
      case "p_name":
        errorfield.p_name = e.target.value == "" ? "required" : "";
        break;
      case "ps_name":
        errorfield.ps_name = e.target.value == "" ? "required" : "";
        break;
      case "category":
        errorfield.category = e.target.value == "" ? "required" : "";
        break;
      case "s_category":
        errorfield.s_category = e.target.value == "" ? "required" : "";
        break;
      case "country":
        errorfield.country = e.target.value == "" ? "required" : "";
        break;
      case "c_name":
        errorfield.c_name = e.target.value == "" ? "required" : "";
        break;
      case "Description":
        errorfield.Description = e.target.value == "" ? "required" : "";
        break;
      case "Policy":
        errorfield.Policy = e.target.value == "" ? "required" : "";
        break;
      case "Quantity":
        errorfield.Quantity = e.target.value == "" ? "required" : "";
        break;
      case "Guarantee":
        errorfield.Guarantee = e.target.value == "" ? "required" : "";
        break;
      case "Creation":
        errorfield.Creation = e.target.value == "" ? "required" : "";
        break;
      default:
        break;
    }
    seterrorfield({ ...errorfield });
    setcontact({ ...contact, [e.target.name]: e.target.value });
  };
  const logins_field = (e) => {
    switch (e) {
      case "p_name":
        errorfield.p_name = contact.p_name == "" ? "required" : "";
        break;
      case "ps_name":
        errorfield.ps_name = contact.ps_name == "" ? "required" : "";
        break;
      case "category":
        errorfield.category = contact.category == "" ? "required" : "";
        break;
      case "s_category":
        errorfield.s_category = contact.s_category == "" ? "required" : "";
        break;
      case "country":
        errorfield.country = contact.country == "" ? "required" : "";
        break;
      case "c_name":
        errorfield.c_name = contact.c_name == "" ? "required" : "";
        break;
      case "Description":
        errorfield.Description = contact.Description == "" ? "required" : "";
        break;
      case "product_file":
        errorfield.product_file = contact.product_file == "" ? "required" : "";
        break;
      // case "Policy":
      //   errorfield.Policy = contact.Policy == "" ? "required" : "";
      //   break;
      case "Quantity":
        errorfield.Quantity = contact.Quantity == "" ? "required" : "";
        break;
      case "Guarantee":
        errorfield.Guarantee = contact.Guarantee == "" ? "required" : "";
        break;
      case "Creation":
        errorfield.Creation = contact.Creation == "" ? "required" : "";
        break;
      default:
        break;
    }
    seterrorfield({ ...errorfield });
    setcontact({ ...contact });
  };
  const navigate = useNavigate()
  const [answerArray, setanswerArray] = useState([]);

  const add_product = () => {
    var myHeaders = new Headers();
    myHeaders.append(
      "Authorization",
      "Bearer " + localStorage.getItem("token")
    );
    var formdata = new FormData();
    formdata.append("product_name", contact.p_name);
    formdata.append("product_short_name", contact.ps_name);
    formdata.append("category", contact.category);
    formdata.append("sub_cat", contact.s_category);
    // formdata.append("country", contact.country);
    // formdata.append("company_name", contact.c_name);
    formdata.append("product_dec", contact.Description);
    formdata.append("made_in", contact.country);
    formdata.append("price_range", contact.c_name);
    formdata.append("price_policy", contact.Policy);
    formdata.append("min_quantity", contact.Quantity);
    formdata.append("guarantee", contact.Guarantee);
    formdata.append("date_of_creation", contact.Creation);
    formdata.append("youtube_link", contact.yt_link);
    contact.product_file?.map((data) => {
      formdata.append("documents[]", data);
    });
    answerArray?.map((question, index) => {
      formdata.append(`product_question[${index}][id]`, question?.questionId);
      formdata.append(`product_question[${index}][answer]`, question?.answer);
    });

    // formdata.append("documents[]", fileInput.files[0], "/C:/Users/HP/Downloads/Careable Plan Management Advert .docx");
    // formdata.append("documents[]", fileInput.files[0], "/C:/Users/HP/Downloads/63da574cbe414.png");

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: formdata,
      redirect: "follow",
    };
console.log("api hit");
    fetch(api + "/api/add_product", requestOptions)
      .then((response) => response.json())
      .then((result) => {
        setImage(result.message)

        toast.success("Product Added successful!")
        
        setTimeout(() => {
          
          navigate("/supplier-product-showcase")
          window.location.reload()
          // window.location.reload()
        }, 2000);
      })
      .catch((error) => 
      {
        toast.error(error.message)
        console.log("error", error)
      }
     );
  };
  useEffect(() => {
    console.log(errorfield, contact, image);
  }, [errorfield, contact, image]);

  const onImageChange = (event) => {
    if (event.target.files && event.target.files[0]) {
      if (contact?.product_file.filter((e) => e.type.split("/")[0] === "image").length < 5 && event.target.files[0].type.split("/")[0] === "image") {
        contact.product_file.push(event.target.files[0]);
        setTimeout(() => {
          setcontact({ ...contact });
        }, 400);
      }
      else if (contact?.product_file.filter((e) => e.type.split("/")[0] === "application").length < 1 && event.target.files[0].type.split("/")[0] === "application") {
        contact.product_file.push(event.target.files[0]);
        setTimeout(() => {
          setcontact({ ...contact });
        }, 400);
      }
    }
  };
  const deletedata = (a) => {
    let x = contact?.product_file;
    x.splice(a, 1);
    setcontact({ ...contact, product_file: x });
  };
  const check_data = [
    { name: "p_name" },
    { name: "ps_name" },
    { name: "category" },
    { name: "s_category" },
    { name: "country" },
    { name: "c_name" },
    { name: "Description" },
    { name: "product_file" },
    // { name: "Policy" },
    { name: "Quantity" },
    // { name: "Guarantee" },
    { name: "Creation" },
  ];
  const [question, setquestion] = useState([]);
  //   console.log(question, "<<<<<<<");
  const [check, setcheck] = useState(true);
  const question_data = () => {
    var myHeaders = new Headers();
    myHeaders.append(
      "Authorization",
      "Bearer " + localStorage.getItem("token")
    );
    var requestOptions = {
      method: "GET",
      headers: myHeaders,
      redirect: "follow",
    };
    fetch(api + "/api/v1/products_questions", requestOptions)
      .then((response) => response.json())
      .then((result) => setquestion(result.data))
      .catch((error) => console.log("error", error));

    // fetch(api + "/api/featuredpost", requestOptions)
    //     .then(response => response.json())
    //     .then(result => setfeatured(result.data.featuredpost))
    //     .catch(error => console.log('error', error));
  };
  useEffect(() => {
    if (check) {
      question_data();
      setcheck(false);
    }
    console.log(subcategory);
  }, [check, subcategory]);

  const [answer2, setanswear2] = useState("");
  const [answer, setanswear] = useState([
    {
      answer: "",
      questionId: "",
    },
  ]);


  console.log(answer2 ,"<<<<");



  const handlequestion = (e, id) => {
    // console.log(e.target.checked ,e.target.value ,id);
    answerArray?.map((item)=>{
      console.log(item);
      if(item?.answer== ""){
        setanswear2("Error") 
      }
    })
    if (answerArray.filter((data) => data.questionId == id)[0]) {
      answerArray.filter((data) => data.questionId == id)[0].answer = e.target.value
    } else {
      answerArray.push({ answer: e.target.value, questionId: id });
    }

    };
    
    useEffect(()=>{
  console.log(answerArray);
  
},[answerArray])



  const selcetcate = (e) => {
    category.filter((data) => {
      if (data.category == e) {
        setsubcategory(data.subcategory)
      }
    })
  }
  return (
    <>

<ToastContainer
        position="top-center"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable={false}
        pauseOnHover
      />
      <div class="product_research_wrap add_new_product Meeting_wrap profile_popup">
        <div class="main">
          {/* <Left_menu/> */}
          <div class={"active router-body"}>
            <div class="breadcrumbs" data-aos="fade-down">
              <ul>
                <li>
                  <a href="/dashboard/user-manegment">Dashboard </a>
                </li>
                <li>
                  <a href="#">
                    {localStorage.getItem("user_type") == "Both"
                      ? props.supplier
                      : localStorage.getItem("user_type")}
                  </a>
                </li>
                <li>
                  <a href="#">
                    <span>Product Showcase</span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <span>Add New Product</span>
                  </a>
                </li>
              </ul>
            </div>
            <div class="product_prfile">
              <h1>Add New Product</h1>
              <div class="row">
                <div class="col_left">
                  <div class="panel">
                    <div class="form-group full">
                      <input
                        type="text"
                        placeholder="Product Full Name"
                        class="form-control"
                        name="p_name"
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.p_name == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                    <div class="form-group full">
                      <input
                        type="text"
                        placeholder="Product Short Name"
                        class="form-control"
                        name="ps_name"
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.ps_name == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                    <div class="form_wrap row">
                      <div class="column">
                        <div class="custom-select">
                          <select
                            name="category"
                            onChange={(e) => {
                              logins_field2(e)
                              selcetcate(e.target.value)
                            }}
                            style={
                              errorfield?.category == ""
                                ? {}
                                : { borderBottom: "1px solid red" }
                            }
                          >
                            <option value="">Category</option>
                            {
                              category.map((data, index) => {
                                return (
                                  <option value={data.category} >{data.category}</option>
                                )
                              })
                            }
                          </select>
                        </div>
                      </div>
                      <div class="column">
                        <div class="custom-select">
                          <select
                            name="s_category"
                            onChange={(e) => logins_field2(e)}
                            style={
                              errorfield?.s_category == ""
                                ? {}
                                : { borderBottom: "1px solid red" }
                            }
                          >
                            <option value="">Sub-Category</option>
                            {
                              subcategory.map((data, index) => {
                                return (
                                  <option value={data.category} >{data.category}</option>
                                )
                              })
                            }
                          </select>
                        </div>
                      </div>
                      <div class="column">
                        <div class="custom-select">
                          <select
                            name="country"
                            onChange={(e) => logins_field2(e)}
                            style={
                              errorfield?.country == ""
                                ? {}
                                : { borderBottom: "1px solid red" }
                            }
                          >
                            <option value="" >Made In</option>

                            {
                              country.data.map((data, i) => {
                                return <option value={data.country}>{data.country}</option>
                              })
                            }
                          </select>
                        </div>
                      </div>
                    </div>
                    <div class="form-group full">
                      <input
                        type="text"
                        placeholder="Price Range"
                        class="form-control"
                        name="c_name"
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.c_name == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                    <textarea
                      maxLength="100"
                      className="form-control"
                      name="Policy"
                      placeholder="Price Policy"
                      onChange={(e) => logins_field2(e)}
                      style={
                        errorfield?.Policy == ""
                          ? {}
                          : { borderBottom: "1px solid red" }
                      }
                    ></textarea>
                    <p class="limit">{contact?.Policy.length}/100</p>
                    <div class="form-group full">
                      <input
                        type="text"
                        placeholder="Minimum Quantity"
                        class="form-control"
                        name="Quantity"
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.Quantity == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                    <div class="form-group full">
                      <input
                        type="text"
                        placeholder="Guarantee"
                        className="form-control"
                        name="Guarantee"
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.Guarantee == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                    <div class="form-group full">
                      <input
                        type="text"
                        placeholder="Date Of Creation"
                        className="form-control"
                        name="Creation"
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.Creation == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                    <textarea
                      maxLength="250"
                      className="form-control"
                      name="Description"
                      placeholder="Product Details Description"
                      onChange={(e) => logins_field2(e)}
                      style={
                        errorfield?.Description == ""
                          ? {}
                          : { borderBottom: "1px solid red" }
                      }
                    ></textarea>
                    <p class="limit">{contact.Description?.length}/250</p>
                  </div>
                  {console.log(question)}
                  {question?.map((quest, index) => {
                    // console.log(quest, "<<<<<<,");
                    return (
                      <div class="radio_section">
                        <p>
                          Q {index + 1}.{quest?.question}
                        </p>
                        <div class="radio_btn">
                          {quest?.type == "Subjective" || quest?.type.toLowerCase() === "textarea" ? (
                            <textarea
                              className="form-control"
                              name="Policy"
                              placeholder="Your Answer"
                              onChange={(e) => handlequestion(e, quest.id)}
                            // style={
                            //   errorfield?.Policy == ""
                            //     ? {}
                            //     : { borderBottom: "1px solid red" }
                            // }
                            ></textarea>
                          ) : (
                            ""
                          )}
                          {
                            quest?.type.toLowerCase() === "select" ?
                              <div class="custom-select">
                                <select className="" >
                                  <option>Select</option>
                                  {quest?.ques_obj?.map((option) => {
                                    return <option value={option}>{option}</option>
                                  })
                                  }
                                </select>
                              </div>
                              :
                              <>
                                {quest?.ques_obj?.map((option) => {
                                  if (option != null) {
                                    return (
                                      <div class="align-items-center">
                                        <input
                                          type={quest?.type.toLowerCase() !== "objective" ? quest?.type.toLowerCase() : "radio"}
                                          id="Lorem Ipsum A"
                                          name={"fav_language" + index}
                                          value={option}
                                          onClick={(e) => handlequestion(e, quest.id)}
                                        />
                                        <label for="Lorem Ipsum A">{option}</label>
                                      </div>
                                    );
                                  }
                                })}
                              </>
                          }

                        </div>
                      </div>
                    );
                  })}

                

               
                </div>
                <div class="col_right">
                  <h6>Product Images</h6>
                  <div
                    class="data_upload"
                    style={
                      errorfield?.product_file == ""
                        ? {}
                        : { borderBottom: "1px solid red" }
                    }
                  >
                    <input
                      type={"file"}
                      name="product_file"
                      onChange={(e) => {
                        seterrorfield({ ...errorfield, product_file: "" });
                        onImageChange(e);
                      }}
                      accept=".xlsx,.xls,image/*,.doc, .docx,.ppt, .pptx,.txt,.pdf,application/msword, application/vnd.openxmlformats-officedocument.wordprocessingml.document, application/vnd.ms-powerpoint, application/vnd.openxmlformats-officedocument.presentationml.slideshow, application/vnd.openxmlformats-officedocument.presentationml.presentation"
                    />
                    <img src="images/profile_upload.svg" alt="" />
                    <h4>
                      Upload Image or <br />
                      Document
                      <br />
                      <p>
                        Image , Document Size : 800kb
                      </p>
                      <p>
                        Can upload maximum 5 images and 1 document.
                      </p>

                    </h4>
                  </div>
                  <div class="thumbnail_section">
                    <h6>Add Video Link</h6>
                    <div class="form-group full">
                      <input
                        type="text"
                        placeholder="https://youtu.be/yAoLSRbzxL"
                        class="form-control"
                        name="yt_link"
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.c_name == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                  </div>
                  <div class="thumbnail_section">
                    <h6>Set Thumbnail Image</h6>
                    {contact?.product_file.map((data, index) => {
                      if (data["type"].split("/")[0] === "image") {
                        return (
                          <div class="thumb_inner row align-items-center">
                            <input
                              type="radio"
                              id="profile"
                              name="thumb"
                              value=""
                              onChange={(e) => logins_field2(e)}
                            />
                            <figure class="center">
                              <img src={URL.createObjectURL(data)} alt="" />
                            </figure>
                            <p>{data.name}</p>
                            <figure
                              onClick={(e) => {
                                deletedata(index);
                              }}
                            >
                              <img src="images/trash-2.svg" alt="" />
                            </figure>
                          </div>
                        );
                      }
                    })}
                  </div>
                  <div class="doc_upload">
                    <h6>Uploaded Documents</h6>
                    {contact?.product_file.map((data, index) => {
                      if (data["type"].split("/")[0] === "application") {
                        return (
                          <div class="inner_doc row align-items-center">
                            <figure>
                              <img src={"images/pdf_icon.png"} alt="" />
                            </figure>
                            <p>{data.name}</p>
                            <figure
                              onClick={(e) => {
                                deletedata(index);
                              }}
                            >
                              <img src="images/trash-2.svg" alt="" />
                            </figure>
                          </div>
                        );
                      }
                    })}
                  </div>
                 
                </div>
              </div>
              <p style={{ display: "block", color: "green", fontWeight: "700" }}>{image}</p>
              {/* <p style={{ display: "block", color: "green" }}>{
              errorfield.product_file == [] ?
              "Image Field id"  + errorfield.product_file + "?"
               :""}</p> */}
              <div class="button_wrap row">
                <button
                  // disabled={image != "" ? true : false}
                  class="btn btn-secondary"
                  onClick={(e) => {

                    if (
                      contact.p_name != "" &&
                      contact.ps_name != "" &&
                      contact.category != "" &&
                      contact.s_category != "" &&
                      contact.country != "" &&
                      contact.c_name != "" &&
                      contact.Description != "" &&
                      contact.product_file != "" &&
                      // contact.thumb != "" &&
                      contact.Policy != "" &&
                      contact.Quantity != "" &&
                      // contact.Guarantee != "" &&
                      // answer2 != "" &&
                      contact.Creation != ""
                    ) {
                      add_product();
                    } else {
                      check_data?.map((data) => {
                        logins_field(data.name);
                        window.scrollTo(0, 100);
                      });
                      // setAnswerError("Please ill all Question")
                    }
                  }}
                >
                  Submit
                </button>
                <a href="" class="btn btn-primary">
                  Cancel
                </a>
              </div>
              <div class="error-button row justify-content-center">
                <a
                  class="error_icon"
                  href="/supplier-product-showcase"
                 
                >
                  <i class="fa fa-arrow-left left" aria-hidden="true"></i>Back
                  to Product Showcase
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Add_new_product;
