import React from 'react'

import { Elements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import { CheckoutForm } from '../stripe/Checkout';
import { useLocation } from "react-router-dom";
import { CheckoutForm2 } from '../stripe/Checkout2';
const stripePromise = loadStripe('pk_test_51MYmQELa6FsGayACSaqncdhfUf2nWTQfnts0BB3xpcQTigD01xCBv86Hkla7PHISvhENQ53qh7vpHPZflvWp9CGb00bfoLpOV6');


function Payment(props) {
    const { state } = useLocation();
    return (
        <>
            <Elements stripe={stripePromise}>
                <CheckoutForm2 data={"20"} amount={state.amount} plan={state.plan} />
            </Elements>
            {/* state.amount */}
        </>
    )
}
export default Payment