import React, { useEffect, useState } from "react";
import { api } from "../base_url";
import Left_menu from "../productpages/left_menu";
import Select from "react-select";
import moment from "moment";
import InputWithButton from "../../components/input-with-button/input-with-button";
import { country } from "../dashboard/country";
import { ToastContainer, toast } from "react-toastify";
import Left_menu2 from "./Left_menu2";
import { useNavigate, useParams } from "react-router-dom";
import pdficon from "../../assets/images/pdf_icon.png";
import trash from "../../assets/images/trash-2.svg";
import axios from "axios";
const emailRegex = RegExp(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/);
const options2 = [];
const url = RegExp(
  "^((((h|H)(t|T)|(f|F))(t|T)(p|P)((s|S)?)://[-.\\w]*)|(((w|W){3}\\.)[-.\\w]+))(/?)([-\\w.?,:'/\\\\+=&;%$#@()!~]*)?$"
);

function Company_profile_Edit(props) {
  const [image, setImage] = useState("");
  const { id } = useParams();
  const navigate = useNavigate();
  const [sidebar, setsidebar] = useState(true);
  const [psname, setpsname] = useState("");
  const [sectors2, setsectors2] = useState([]);
  const [options, setoptions] = useState([]);
  const [selectOptions, setSelectOptions] = useState([]);
  const [answerArray, setanswerArray] = useState([]);


 const sectors_array = []
 selectOptions?.map((item)=>{
   sectors_array?.push(item?.value)
  })

  console.log( sectors_array,"selectOptionsselectOptions");

  const [contact, setcontact] = useState({
    p_name: "",
    ps_name: "",
    category: "",
    s_category: "",
    country: "",
    c_name: "",
    Description: "",
    product_file: [],
    product_file2: [],
    thumb: "",
    Policy: "",
    Quantity: "",
    sector_name: "",
    Creation: "",
    price_range: "",
    questions: [],
  });
  const [errorfield, seterrorfield] = useState({
    p_name: "",
    ps_name: "",
    category: "",
    s_category: "",
    country: "",
    c_name: "",
    Description: "",
    product_file: [],
    thumb: "",
    Policy: "",
    Quantity: "",
    sector_name: "",
    Creation: "",
  });
  const logins_field2 = (e) => {
    switch (e.target.name) {
      case "p_name":
        errorfield.p_name = e.target.value == "" ? "required" : "";
        break;
      case "ps_name":
        errorfield.ps_name = e.target.value == "" ? "required" : "";
        break;
      case "category":
        errorfield.category = e.target.value == "" ? "required" : "";
        break;
      case "s_category":
        errorfield.s_category = e.target.value == "" ? "required" : "";
        break;
      case "country":
        errorfield.country = e.target.value == "" ? "required" : "";
        break;
      case "c_name":
        errorfield.c_name = e.target.value == "" ? "required" : "";
        break;
      case "Description":
        errorfield.Description = e.target.value == "" ? "required" : "";
        break;
      case "Policy":
        errorfield.Policy = e.target.value == "" ? "required" : "";
        break;
      case "Quantity":
        errorfield.Quantity = e.target.value == "" ? "required" : "";
        break;
      case "Guarantee":
        errorfield.Guarantee = e.target.value == "" ? "required" : "";
        break;
      case "Creation":
        errorfield.Creation = e.target.value == "" ? "required" : "";
        break;
      default:
        break;
    }
    seterrorfield({ ...errorfield });
    setcontact({ ...contact, [e.target.name]: e.target.value });
  };
  const logins_field = (e) => {
    switch (e) {
      case "p_name":
        errorfield.p_name = contact.p_name == "" ? "required" : "";
        break;
      case "ps_name":
        errorfield.ps_name = contact.ps_name == "" ? "required" : "";
        break;
      case "category":
        errorfield.category = contact.category == "" ? "required" : "";
        break;
      case "s_category":
        errorfield.s_category = contact.s_category == "" ? "required" : "";
        break;
      case "country":
        errorfield.country = contact.country == "" ? "required" : "";
        break;
      case "c_name":
        errorfield.c_name = contact.c_name == "" ? "required" : "";
        break;
      case "Description":
        errorfield.Description = contact.Description == "" ? "required" : "";
        break;
      // case "product_file":
      //   errorfield.product_file = contact.product_file == "" ? "required" : "";
      //   break;
      case "Policy":
        errorfield.Policy = contact.Policy == "" ? "required" : "";
        break;
      case "Quantity":
        errorfield.Quantity = contact.Quantity == "" ? "required" : "";
        break;
      case "Guarantee":
        errorfield.Guarantee = contact.Guarantee == "" ? "required" : "";
        break;
      case "Creation":
        errorfield.Creation = contact.Creation == "" ? "required" : "";
        break;
      default:
        break;
    }
    seterrorfield({ ...errorfield });
    setcontact({ ...contact });
  };

  const [editData, setEditData] = useState([]);
  const [check, setcheck] = useState(false);

console.log(selectOptions);
  const get_companyinfo = async () => {
    await axios
      .get(api + "/api/company-profile", {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
      })
      .then((res) => {
        if (res?.data?.success == true) {
          setTimeout(() => {
            setEditData(res.data?.data);
            console.log(res.data,res?.data?.data.company?.sector, "<<<<<<<,");
            contact.p_name = res?.data?.data.company?.company_name;
            contact.ps_name = res?.data?.data.company?.company_short_name;
            contact.country = res?.data?.data.company?.country;
            contact.Description = res?.data?.data.company?.company_dec;
            // contact.sector_name = res?.data?.data.company?.sector;
            if (
              /^[\],:{}\s]*$/.test(
                res?.data?.data.company?.sector?.replace(/\\["\\\/bfnrtu]/g, "@")
                  .replace(
                    /"[^"\\\n\r]*"|true|false|null|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?/g,
                    "]"
                  )
                  .replace(/(?:^|:|,)(?:\s*\[)+/g, "")
              )
            ){
              const arrayof = JSON.parse(res?.data?.data.company?.sector)
              arrayof.map((item)=>{
                selectOptions.push({value : item , label:item})
              })


                    }

            contact.Creation = res?.data?.data.company?.company_short_name;
            contact.product_file2 = res.data?.data?.media_files;
            contact.questions = res?.data?.data.questions;
            contact.yt_link =
              res?.data?.data.company?.youtube_link != null
                ? res?.data?.data.company?.youtube_link
                : "";

            res?.data?.data.questions?.map((item) => {
              if (item.type?.toLowerCase() == "checkbox") {
                if (
                  /^[\],:{}\s]*$/.test(
                    item?.answer
                      .replace(/\\["\\\/bfnrtu]/g, "@")
                      .replace(
                        /"[^"\\\n\r]*"|true|false|null|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?/g,
                        "]"
                      )
                      .replace(/(?:^|:|,)(?:\s*\[)+/g, "")
                  )
                ) {
                  const ans22 = JSON.parse(item?.answer);
                  options?.push(...ans22);
                }
              }
            });
          }, 80);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
 return ()=>{
      get_companyinfo();
 }
  }, []);

  useEffect(() => {
    setpsname(editData?.product_name);
  }, []);

  // let options = []
  const [optionsedit, setoptions2] = useState([]);
  function removeDuplicates(arr) {
    var unique = [];
    for (let i = 0; i < arr.length; i++) {
      if (unique.indexOf(arr[i]) === -1) {
        unique.push(arr[i]);
      }
    }
    return unique;
  }

  function removeItemAll(arr, value) {
    var i = 0;
    while (i < arr.length) {
      if (arr[i] === value) {
        arr.splice(i, 1);
      } else {
        ++i;
      }
    }
    return arr;
  }
  const handlequestion2 = (e, id, type, pqId) => {
    if (type?.toLowerCase() == "checkbox") {
      if (
        options
          ?.filter((item) => {
            return item == e.target.value;
          })[0]
          ?.toLowerCase() === e.target.value.toLowerCase()
      ) {
        const index = options.indexOf(e.target.value);
        if (index > -1) {
          // only splice array when item is found
          options?.splice(index, 1); // 2nd parameter means remove one item only

          removeItemAll(options, e.target.value);
        } // options??.pop();
      } else {
        options.push(e.target.value);
      }
      removeDuplicates(options);
      setoptions2(removeDuplicates(options));
    }
  };

  const edit_product = () => {
    var myHeaders = new Headers();
    myHeaders.append(
      "Authorization",
      "Bearer " + localStorage.getItem("token")
    );
    var formdata = new FormData();
    formdata.append("company_name", contact.p_name);
    formdata.append("company_short_name", contact.ps_name);
    formdata.append("company_dec", contact.Description);
    formdata.append("country", contact.country);
    formdata.append("sector", JSON.stringify(sectors_array));
    formdata.append("youtube_link", contact.yt_link);
    formdata.append("id", editData?.company?.id);

    answerArray?.map((question, index) => {
      // console.log(question);
      formdata.append(`company_question[${index}][id]`, question?.questionId);
      formdata.append(
        `company_question[${index}][answer]`,
        question?.questionPqId == 42
          ? JSON.stringify(optionsedit)
          : question?.answer
      );
      formdata.append(
        `company_question[${index}][pqid]`,
        question?.questionPqId
      );
    });

    contact.product_file.map((data) => {
      // console.log(data);
      formdata.append("documents[]", data);
    });

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: formdata,
      redirect: "follow",
    };

    fetch(api + "/api/edit-company-profile", requestOptions)
      .then((response) => response.json())
      .then((result) => {
        if (result.success == true) {
          toast.success("Company profile updated successful!");
          setTimeout(() => {
            navigate("/buyer-company-profile");
            // window.location.reload();
          }, 2000);
        }
        setImage(result.message);
        if (result?.success == false) {
          toast.error("Please fill all required fields !");
          window.scrollTo(0, 0);
          setTimeout(() => {
            setImage("");
          }, 5000);
        }
      })
      .catch((error) => {
        toast.error("Error With status code 500 !");
        // console.log("error", error);
      });
  };
  useEffect(() => {
    console.log(errorfield);
  }, [errorfield, contact, image, contact]);

  const onImageChange = (event) => {
    if (event.target.files && event.target.files[0]) {
      contact.product_file.push(event.target.files[0]);
      setTimeout(() => {
        setcontact({ ...contact });
      }, 400);
    }
  };
  const deletedata = (a) => {
    let x = contact?.product_file;
    x.splice(a, 1);
    setcontact({ ...contact, product_file: x });
  };
  const check_data = [
    { name: "p_name" },
    { name: "ps_name" },
    { name: "category" },
    { name: "s_category" },
    { name: "country" },
    { name: "c_name" },
    { name: "Description" },
    { name: "sector_name" },
    { name: "Policy" },
    { name: "Quantity" },
    { name: "Guarantee" },
    { name: "Creation" },
  ];

  const delete_image = (id) => {
    var myHeaders = new Headers();
    myHeaders.append(
      "Authorization",
      "Bearer " + localStorage.getItem("token")
    );
    var requestOptions = {
      method: "GET",
      headers: myHeaders,
      redirect: "follow",
    };
    fetch(api + `/api/v1/imagedelete?image_id=${id}`, requestOptions)
      .then((response) => response.json())
      .then((result) => {
        if (result?.success == true) {
          // fetch_editProductdetails();
        }
      })
      .catch((error) => console.log("error", error));
  };

  const handlequestion = (e, id, pq_id) => {
    if (answerArray.filter((data) => data.questionId == id)[0]) {
      answerArray.filter((data) => data.questionId == id)[0].answer =
        e.target.value;
    } else {
      answerArray.push({
        answer: e.target.value,
        questionId: id,
        questionPqId: pq_id,
      });
    }
  };

const [givenoptions ,setgivenoptions] =useState()
const companySectoroptions = [];

  givenoptions?.map((item)=>{
    companySectoroptions.push({value: item?.category_name , label : item?.category_name})
  })

  useEffect(() => {
      axios
        .get(api + "/api/sector-category")
        .then((response) => {
          const short_list = response.data.data;
              setgivenoptions(short_list)})
        .catch((error) => {
          // handle error here, e.g. set error state
          console.log(error);
        });
    

  }, []);


  const controlStyle ={
    control: (provided) => ({
      ...provided,
      height : "48px",
      borderRadius: "33px",
      '&:hover': {
        border: 'solid 1px #D9D9D9',
        boxShadow: 'none',
        borderRadius: "33px"

      },
      '&:focus': {
        border: 'solid 1px #D9D9D9',
        boxShadow: 'none',
        borderRadius: "33px"

      },
      "&:acitve": {
          border: "solid 1px #D9D9D9",
          boxShadow: "none",
          borderRadius: "33px"

        }
    }),
  };
  


  return (
    <>
      <ToastContainer
        position="top-center"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable={false}
        pauseOnHover
      />
      <div className="product_research_wrap2 product_research_wrap add_new_product Meeting_wrap profile_popup">
        <div className="main">
          <Left_menu sidebar={sidebar} setsidebar={setsidebar} />
          <div className={sidebar ? "active router-body" : "router-body"}>
            <div className="breadcrumbs" data-aos="fade-down">
              <ul>
                <li>
                  <a href="/dashboard">Dashboard </a>
                </li>
                <li>
                  <a href="/dashboard">Buyer</a>
                </li>
                <li>
                  <a
                    href="#"
                    onClick={() => {
                      navigate("/buyer-company-profile");
                    }}
                  >
                    <span>My Company Profile</span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <span>Edit Company Profile</span>
                  </a>
                </li>
              </ul>
            </div>
            <div className="product_prfile">
              <h1>Edit Company Profile</h1>
              <div className="row">
                <div className="col_left">
                  <div className="panel">
                    <div className="form-group full">
                      <input
                        type="text"
                        placeholder="Company Full Name *"
                        className="form-control"
                        name="p_name"
                        value={contact?.p_name}
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.p_name == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                    <div className="form-group full">
                      <input
                        type="text"
                        placeholder="Company Short Name *"
                        className="form-control"
                        name="ps_name"
                        value={contact?.ps_name}
                        onChange={(e) => logins_field2(e)}
                        style={
                          errorfield?.ps_name == ""
                            ? {}
                            : { borderBottom: "1px solid red" }
                        }
                      />
                    </div>
                    <div className="setrowcustom form_wrap full">
                      <div className="form-group">
                        <div
                          className="custom-select"
                          style={
                            errorfield?.country == ""
                              ? {}
                              : { borderBottom: "1px solid red" }
                          }
                        >
                          <select
                            name="country"
                            value={contact?.country}
                            onChange={(e) => logins_field2(e)}
                          >
                            <option>Country *</option>
                            {country.data.map((data, i) => {
                              return (
                                <option value={data.country}>
                                  {data.country}
                                </option>
                              );
                            })}
                            {/* <option>india</option> */}
                          </select>
                        </div>
                      </div>
                      <div className="Company_sector form-group ">
                      <Select
                      isObject={false}
                      options={companySectoroptions}
                      defaultValue={selectOptions}
                      isMulti
                      className="Company_sector2 basic-multi-select"
                      classNamePrefix="select"
                      onChange={(e)=>setSelectOptions(e)}                
                      placeholder="Company Sector *"
                      styles={controlStyle}
                      //  value={companySectoroptions}
                    />


                        {/* <input
                          type="text"
                          placeholder="Company Sector *"
                          className="form-control"
                          name="sector_name"
                          value={contact.sector_name}
                          onChange={(e) => logins_field2(e)}
                          style={
                            errorfield?.sector_name == ""
                              ? {}
                              : { borderBottom: "1px solid red" }
                          }
                        /> */}




                      </div>
                    </div>
                    <textarea
                      className="form-control"
                      placeholder="Company Profile Description *"
                      name="Description"
                      value={contact.Description}
                      onChange={(e) => logins_field2(e)}
                      style={
                        errorfield?.Description == ""
                          ? {}
                          : { borderBottom: "1px solid red" }
                      }
                    ></textarea>
                    <p class="limit">
                      {contact.Description?.length + "/" + "250"}
                    </p>
                  </div>
                  {editData.questions?.map((item, index) => {
                    // console.log(item);
                    return (
                      <div className="radio_section">
                        <p>
                          Q {index + 1}.{item?.question}
                        </p>
                        <div className="radio_btn">
                          {item?.type == "Subjective" ||
                          item?.type.toLowerCase() === "textarea" ? (
                            <textarea
                              className="form-control"
                              name="Policy"
                              placeholder="Your Answer *"
                              defaultValue={
                                contact?.questions?.filter(
                                  (data) =>
                                    data?.company_question_id ==
                                    item?.company_question_id
                                )[0]?.answer
                              }
                              onChange={(e) =>
                                handlequestion(
                                  e,
                                  item.id,
                                  item?.company_question_id
                                )
                              }
                              //  style={
                              //    contact?.questions?.filter(
                              //     (data) =>
                              //     data?.product_question_id ==
                              //     item?.product_question_id
                              // )[0]?.answer != ""
                              //    ? {}
                              //     : { borderBottom: "1px solid red" }
                              // }
                            ></textarea>
                          ) : (
                            ""
                          )}
                          {item?.type.toLowerCase() === "select" ? (
                            <div className="custom-select">
                              <select className="">
                                <option>Select</option>
                                {item?.ques_obj?.map((option) => {
                                  return (
                                    <option value={option}>{option}</option>
                                  );
                                })}
                              </select>
                            </div>
                          ) : (
                            <>
                              {item?.objects?.map((option, indexkey) => {
                                // console.log(contact?.questions ,item);
                                if (item?.type.toLowerCase() == "checkbox") {
                                  try {
                                    if (
                                      /^[\],:{}\s]*$/.test(
                                        item?.answer
                                          .replace(/\\["\\\/bfnrtu]/g, "@")
                                          .replace(
                                            /"[^"\\\n\r]*"|true|false|null|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?/g,
                                            "]"
                                          )
                                          .replace(/(?:^|:|,)(?:\s*\[)+/g, "")
                                      )
                                    ) {
                                      var ans = JSON.parse(item?.answer);
                                    }
                                  } catch (error) {
                                    console.log(error);
                                  }
                                  // console.log(ans);
                                }
                                if (option != null) {
                                  return (
                                    <div className="align-items-center">
                                      <input
                                        // id={}
                                        type={
                                          item?.type.toLowerCase() !==
                                          "objective"
                                            ? "Checkbox"
                                            : "radio"
                                        }
                                        id={"op" + indexkey}
                                        name={"fav_language" + indexkey}
                                        defaultChecked={
                                          ans
                                            ?.filter(
                                              (item) =>
                                                item?.toUpperCase() ==
                                                option?.toUpperCase()
                                            )[0]
                                            ?.toUpperCase() ==
                                          option?.toUpperCase()
                                        }
                                        defaultValue={option}
                                        onClick={(e) => {
                                          handlequestion(
                                            e,
                                            item.id,
                                            item?.company_question_id
                                          );
                                          handlequestion2(
                                            e,
                                            item.id,
                                            item?.type,
                                            item?.company_question_id
                                          );
                                        }}
                                      />
                                      <label for="Lorem Ipsum A">
                                        {option}
                                      </label>
                                    </div>
                                  );
                                }
                              })}
                            </>
                          )}
                        </div>
                      </div>
                    );
                  })}
                </div>
                <div className="col_right">
                  <h6>Product Images</h6>
                  <div
                    className="data_upload"
                    // style={
                    //   errorfield?.product_file == ""
                    //     ? {}
                    //     : { borderBottom: "1px solid red" }
                    // }
                  >
                    <input
                      type={"file"}
                      name="product_file"
                      onChange={(e) => {
                        seterrorfield({ ...errorfield, product_file: "" });
                        onImageChange(e);
                      }}
                      accept=".xlsx,.xls,image/*,.doc, .docx,.ppt, .pptx,.txt,.pdf,application/msword ,application/pdf, application/vnd.openxmlformats-officedocument.wordprocessingml.document, application/vnd.ms-powerpoint, application/vnd.openxmlformats-officedocument.presentationml.slideshow, application/vnd.openxmlformats-officedocument.presentationml.presentation"
                    />
                    <img src="images/profile_upload.svg" alt="" />
                    <h4>
                      Upload Image or <br />
                      Document
                      <br />
                      <p> Image , Document Size : 800kb </p>
                      <p>Can upload maximum 5 images and 1 document.</p>
                    </h4>
                  </div>
                  <div className="thumbnail_section">
                    <h6>Add Video Link</h6>
                    <div className="form-group full">
                      <input
                        type="text"
                        placeholder="https://youtu.be/yAoLSRbzxL"
                        className="form-control"
                        value={contact.yt_link == "null" ? "" : contact.yt_link}
                        name="yt_link"
                        onChange={(e) => logins_field2(e)}
                      />
                    </div>
                  </div>
                  <div className="thumbnail_section">
                    <h6>Set Thumbnail Image</h6>
                    {contact?.product_file?.map((data, index) => {
                      // console.log(data);
                      if (data["type"]?.split("/")[0] === "image") {
                        return (
                          <div className="thumb_inner row align-items-center">
                            <input
                              type="radio"
                              id={"profile" + data?.image_id}
                              name="thumb"
                              value=""
                              checked={index === 0}
                            />
                            <figure className="center">
                              <img
                                src={
                                  URL.createObjectURL(data)
                                    ? URL.createObjectURL(data)
                                    : data?.file_path
                                }
                                alt=""
                              />
                            </figure>
                            <p>{data?.name}</p>

                            <figure
                              onClick={(e) => {
                                deletedata(index);
                              }}
                            >
                              <img src={trash} alt="" />
                            </figure>
                          </div>
                        );
                      }
                    })}
                    {contact?.product_file2?.map((data, index) => {
                      if (data?.media_type == "image") {
                        return (
                          <div className="thumb_inner row align-items-center">
                            <input
                              type="radio"
                              id={"profile" + index}
                              name="thumb"
                              value={index}
                              defaultchecked={index === 0}
                            />
                            <figure className="center">
                              <img src={data?.file_path} alt="" />
                            </figure>
                            <p>{"Uploaded images"}</p>
                            <figure
                              onClick={(e) => {
                                delete_image(data?.image_id);
                                deletedata(index);
                              }}
                            >
                              <img src={trash} alt="" />
                            </figure>
                          </div>
                        );
                      }
                    })}
                    {/* {
     editData?.media_files.map((data, index) => {
        if (data['type']?.split('/')[0] === 'image') {
            return (
                <div className="thumb_inner row align-items-center">
                    <input type="radio" id="profile" name="thumb" value="" />
                    <figure className="center"><img src={URL.createObjectURL(data)} alt="" /></figure>
                    <p>{data.name}</p>
                    <figure onClick={e => {
                        deletedata(index)
                    }}><img src="images/trash-2.svg" alt="" /></figure>
                </div>
            )
        }
    })
} */}
                  </div>
                  <div className="doc_upload">
                    <h6>Uploaded Documents</h6>
                    {contact?.product_file2.map((data, index) => {
                      if (data["type"]?.split("/")[0] === "application") {
                        return (
                          <div className="inner_doc row align-items-center">
                            <figure>
                              <img src={pdficon} alt="" />
                            </figure>
                            <p>{data.name}</p>
                            <figure
                              onClick={(e) => {
                                deletedata(index);
                              }}
                            >
                              <img src={trash} alt="" />
                            </figure>
                          </div>
                        );
                      } else if (data?.media_type == "doc") {
                        return (
                          <div className="inner_doc row align-items-center">
                            <figure>
                              <img src={pdficon} alt="" />
                            </figure>
                            <p>{"Uploaded doc"}</p>
                            <figure
                              onClick={(e) => {
                                delete_image(data?.image_id);
                                deletedata(index);
                              }}
                            >
                              <img src={trash} alt="" />
                            </figure>
                          </div>
                        );
                      }
                    })}

                    {contact?.product_file?.map((data, index) => {
                      if (data["type"]?.split("/")[0] === "application") {
                        return (
                          <div className="inner_doc row align-items-center">
                            <figure>
                              <img src={pdficon} alt="" />
                            </figure>
                            <p>{data.name}</p>
                            <figure
                              onClick={(e) => {
                                deletedata(index);
                              }}
                            >
                              <img src={trash} alt="" />
                            </figure>
                          </div>
                        );
                      } else if (data?.media_type == "doc") {
                        return (
                          <div className="inner_doc row align-items-center">
                            <figure>
                              <img src={pdficon} alt="" />
                            </figure>
                            <p>{"Uploaded doc"}</p>
                            <figure
                              onClick={(e) => {
                                delete_image(data?.image_id);
                                deletedata(index);
                              }}
                            >
                              <img src={trash} alt="" />
                            </figure>
                          </div>
                        );
                      }
                    })}
                  </div>
                </div>
              </div>
              <p
                style={{ display: "block", color: "green", fontWeight: "700" }}
              >
                {image}
              </p>
              <div className="button_wrap row">
                <button
                  className="btn btn-secondary"
                  type="button"
                  onClick={(e) => {
                    if (
                      // errorfield?.p_name == ""
                      contact.p_name == "" &&
                      contact.ps_name == "" &&
                      contact.country == "" &&
                      contact.Description == ""
                      // errorfield?.c_name == ""
                    ) {
                      check_data?.map((data) => {
                        logins_field(data.name);
                      });
                      window.scrollTo(0, 100);
                      // alert("error")
                    } else {
                      edit_product();
                    }
                  }}
                >
                  Submit
                </button>
                <a href="/buyer-company-profile" className="btn btn-primary">
                  Cancel
                </a>
              </div>
              <div className="error-button row justify-content-center">
                {/* <a className="error_icon" href="/dashboard/user-manegment">
                  <i className="fa fa-arrow-left left" aria-hidden="true"></i>
                  Back to Product Showcase
                </a> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Company_profile_Edit;
