import React, { useEffect, useState } from "react";
import { api } from "../base_url";

export default function Favourite(props) {
  const [check, setcheck] = useState(true)



const [favrite,setfavrite] =useState()

 const favurate_data = () => {
        var myHeaders = new Headers();
        myHeaders.append('Authorization',"Bearer " + localStorage.getItem("token"))
        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow',
        };
        fetch(api + "/api/v1/favoriteslist", requestOptions)
            .then(response => response.json())
            .then(result => setfavrite(result.data))
            .catch(error => console.log('error', error));
    }
    useEffect(() => {
        // if (check) {
          favurate_data()
        //     setcheck(false)
        // }
    }, [])


console.log(favrite);








  return (
    <div class={(props.sidebar ? "active " : " ") + "router-body"}>
      <div class="breadcrumbs" data-aos="fade-down">
        <ul>
          <li>
            <a href="/dashboard">Dashboard </a>
          </li>
          <li>
            <a href="#">
              Buyer
              {/* {localStorage.getItem("user_type") == "Both" ? props.supplier  : localStorage.getItem("user_type")  }  */}
            </a>
          </li>
          <li>
            <a href="#">
              <span> My Meetings </span>
            </a>
          </li>
          <li>
            <a href="#">
              <span> Favourites List </span>
            </a>
          </li>
        </ul>
      </div>
      <h2>Favourites List</h2>
      <div class="add_product_wrap row justify-content-between">
        <div class="column">
          <div class="search">
            <input type="text" class="form-control" placeholder="Type here" />
          </div>
          <button type="submit" class="btn btn-block btn-secondary">
            Search
          </button>
        </div>
      </div>
      <ol class="favourite_fix sub_category list grid-view-filter" id="product">
     
     {
      favrite?.data?.length == 0? "" :
      <>
      {
       favrite?.data?.map((item,i)=>{
        return(

        <li class="row align-items-center" key={i}>
          <div class="col_img">
            <figure>
              <img src="images/fav_1.jpg" alt="" />
            </figure>
          </div>
          <div class="col_category">
            <div class="row justify-content-between">
              <h4>

                {
                  item?.sub_cat ? item?.sub_cat :"Sub-category"
                }
              </h4>
              <img src="images/heart-icon 2.svg" alt="" />
            </div>

            <ul class="d-flex align-items-center">
              <li>
                <span>
                  <img src="images/Product.svg" alt="" />
                </span>{" "}
                {/* Product Short Name */}
                {
                  item?.product_short_name ? item?.product_short_name :"product_short_name"
                }
              </li>
              <li>
                <span>
                  <img src="images/country.svg" alt="" />
                </span>{" "}
                {
                  item?.made_in ? item?.made_in :" Product Country"
                }
                {/* Product Country */}
              </li>
            </ul>
          </div>
        </li>
        )
       }) 
      }
      
      </>

     }
     
        <li class="row align-items-center">
          <div class="col_img">
            <figure>
              <img src="images/fav_6.jpg" alt="" />
            </figure>
          </div>
          <div class="col_category">
            <div class="row justify-content-between">
              <h4>Sub-category</h4>
              <img src="images/heart-icon 2.svg" alt="" />
            </div>
            <ul class="d-flex align-items-center">
              <li>
                <span>
                  <img src="images/Product.svg" alt="" />
                </span>{" "}
                Product Short Name
              </li>
              <li>
                <span>
                  <img src="images/country.svg" alt="" />
                </span>{" "}
                Product Country
              </li>
            </ul>
          </div>
        </li>
      </ol>
    </div>
  );
}
