import React, { useState } from "react";
import { Navigate, useNavigate, useParams } from "react-router-dom";
import Left_menu2 from "./Left_menu2";

function View_remarkBoth() {
    const { id, usertype } = useParams();
    const [sidebar, setsidebar] = useState(false);
  const navigate =useNavigate()
// const {usertype}  = useParams
  console.log(id);
  return (
    <>
      <div className="edit_remark Meeting_wrap">
        <div className="main">
          <Left_menu2 sidebar={sidebar} setsidebar={setsidebar} />

          <div className={sidebar ? "active router-body" : "router-body"}>
            <div className="breadcrumbs" data-aos="fade-down">
              <ul>
                <li>
                  <a href="/dashboard/user-manegment">Dashboard </a>
                </li>
                <li>
                  <a href="#">{usertype == "buyer" ? "Buyer" : "Supplier"}</a>
                </li>
                <li>
                <a href={`/passed-meeting/${usertype}`}>
                    <span> My Meetings </span>
                  </a>
                </li>
                <li>
                  <a>
                    <span> Passed Meetings </span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <span> View Remark</span>
                  </a>
                </li>
              </ul>
            </div>
            <div className="remark_wrap row justify-content-between">
              <div className="column">
                <h2>View Remark</h2>
              </div>
              <div className="column">
                <p id="edit" onClick={()=>navigate("/edit-remark/2/"+usertype )} style={{ cursor: "pointer" }}>
                  Edit Remark <img src="images/edit (1).svg" alt="" />
                </p>
              </div>
            </div>
            <form action="">
              <div className="form">
                <input
                  type="text"
                  id="text"
                  value="Lorem Ipsum dolor"
                  className="form-control"
                  placeholder=""
                  disabled
                />
              </div>
              <div className="form">
                <textarea
                  name="description"
                  id="textarea"
                  value="Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries,"
                ></textarea>
              </div>
              <div className="btn_wrapper row">
                <a id="back" href="" className="btn btn-secondary">
                  Back
                </a>
                <a
                  id="resubmit"
                  type="submit"
                  href=""
                  className="btn btn-secondary"
                  style={{ display: "none" }}
                >
                  Resubmit
                </a>
                <a
                  id="cancel"
                  href=""
                  className="btn btn-primary"
                  style={{ display: "none" }}
                >
                  Cancel
                </a>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default View_remarkBoth;
